.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
  margin:0;
  padding:0;
  font-family: 'Poppins', sans-serif;
  user-select:none;
  text-align:center;
}
body{
  background:#1a1a1a;
}
.main{
  border:1px solid white;
  border-radius:7px;
  box-shadow:5px 5px 10px white;
background-color: #FF3CAC;
background-image: linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%);
  width: 300px;
  height: 520px;
  position:absolute;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
}
.main h2{
  color:white;
  font-weight:200;
  padding-top:5px;
}
.main hr{
  background:white;
  margin-top:8px;
  height:1px;
  width:70%;
  border:none;
  margin-left:45px;
}
.text,.pass1,.pass2,.email{
  width:230px;
  height:35px;
  border-radius:5px;
  border:none;
  outline:none;
  font-size:16px;
  cursor:default;
}
input::placeholder{
  opacity:0.7;
  font-size:15px;
}
.text{
  margin-top:-10px;
}
.pass1,.pass2,.email{
  margin-top:12px;
}
.btn{
  margin-top:10px;
  width:80px;
  height:40px;
  font-size:17px;
  color:black;
  background:#3EB1FF;
  border:none;
  border-radius:5px;
  cursor:pointer;
  transition:0.5s;
}
.btn:hover{
  background:#134B72;
  transition:0.3s;
  color:white;
  width:120px;
}
.radio{
  color:white;
  font-weight:300;
  accent-color: greenyellow;
}
.radio:hover{
  border-bottom:1px solid white;
}
.radio input{
  margin-top:15px;
  cursor:pointer;
}
.i i{
  padding:5px;
  font-size:25px;
  padding-right:20px;
}
.i button{
  text-align:left;
  width:140px;
  margin-top:18px;
  cursor:pointer;
  background: white;
  border:none;
  border-radius: 5px;
  color:black;
  font-weight:400;
  font-size:15.8px;
  padding-bottom:4px;
  transition:0.4s;
}
.i button:hover{
  font-size:16px;
  transition:0.7s;
}
.fa-google{
  color: red;
}
.fa-github{
  color:black;
}
.fa-twitter{
  color: #00acee;
}
.fa-spotify{
  color: green;
}
.i p{
  color:white;
  margin-bottom:-25px;
}
